import * as THREE from "three";
import gsap from "gsap";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.5, 100000);
camera.aspect = window.innerWidth / window.innerHeight;
camera.updateProjectionMatrix();
camera.position.set(1000, 1000, 1000);
scene.add(camera);

// hdr
const loader = new RGBELoader();
loader.load("./texture/env.hdr", function (texture) {
  // envMap
  texture.mapping = THREE.EquirectangularReflectionMapping;
  scene.background = texture;
  scene.environment = texture;
});

// glb
const gltfLoader = new GLTFLoader();
const dracoLoader = new DRACOLoader();
dracoLoader.setDecoderPath("./draco/");
dracoLoader.setDecoderConfig({ type: "js" });
dracoLoader.preload();
gltfLoader.setDRACOLoader(dracoLoader);
let mixer;
let gltfModel = null;
gltfLoader.load("./model/ship4.glb", function (gltf) {
  console.log(gltf);
  gltfModel = gltf;
  scene.add(gltf.scene);
  // gltf.scenes[0].position = new Vector3(100, 100, 0);
  // gltf.scene.traverse(function (child) {
  //   if (child.name == "Body") {
  //     console.log(child);
  //   }
  //   // if (child.name == "Floor") {
  //   //   child.material = new THREE.MeshStandardMaterial({
  //   //     color: 0xffffff,
  //   //   });
  //   //   console.log(child);
  //   // }
  //   if (child.isMesh) {
  //     child.material.depthWrite = true;
  //     child.material.normalScale = new THREE.Vector2(1, 1);
  //     child.material.side = THREE.FrontSide;
  //     child.material.transparent = false;
  //     // false
  //     child.material.vertexColors = false;
  //   }
  // });
  // 
  mixer = new THREE.AnimationMixer(gltf.scene);
  let action = mixer.clipAction(gltf.animations[0]);
  action.play();
  action = mixer.clipAction(gltf.animations[1]);
  action.play();

  const hemisphereLight = new THREE.HemisphereLight(0xffffbb, 0x080820, 3);
  scene.add(hemisphereLight);

  // ;
  const light = new THREE.DirectionalLight(0xffffff, 5);
  light.position.set(0, 100, 100);
  scene.add(light);

  // 
  // const pointLight = new THREE.PointLight(0xffffff, 5);
  // pointLight.position.set(0, 100, 100);

});



// 
const geometry = new THREE.SphereGeometry(1, 32, 32);
const material1 = new THREE.MeshStandardMaterial({
  color: 0xffffff,
  metalness: 1,
  roughness: 1,
});
const sphere = new THREE.Mesh(geometry, material1);
sphere.position.set(-2, 2, 2);
scene.add(sphere);

// 
const raycaster = new THREE.Raycaster();
//  
const mouse = new THREE.Vector2();
// 
window.addEventListener("click", (event) => {
  // [-1, 1]
  mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
  // [-1, 1] YY - 
  mouse.y = -((event.clientY / window.innerHeight) * 2 - 1);
  // 
  raycaster.setFromCamera(mouse, camera);
  // 
  let result = raycaster.intersectObjects([sphere]);
  // result[0].object.material = redMaterial;
  result.forEach((item) => {
    console.log('click');
    // 
    const womenPosition = gltfModel.scene.position.clone();
    // 
    gsap.to(controls.target, {
      x: womenPosition.x,
      y: womenPosition.y,
      z: womenPosition.z,
      duration: 0.5,
      onComplete: () => {
        // 
        gsap.to(camera.position, {
          x: womenPosition.x + 3,
          y: womenPosition.y + 3,
          z: womenPosition.z + 3,
          duration: 2,
        });
      },
    });
  });
});

const renderer = new THREE.WebGLRenderer();
renderer.setSize(window.innerWidth, window.innerHeight);
renderer.shadowMap.enabled = true;
renderer.physicallyCorrectLights = true;
renderer.setClearColor(0xcccccc, 1);

document.body.appendChild(renderer.domElement);
const controls = new OrbitControls(camera, renderer.domElement);
controls.enableDamping = true;
controls.target = sphere.position.clone();

const axesHelper = new THREE.AxesHelper(5);
scene.add(axesHelper);
const clock = new THREE.Clock();
function render() {
  let time = clock.getDelta();
  if (mixer) {
    mixer.update(time);
  }
  controls.update();
  renderer.render(scene, camera);
  requestAnimationFrame(render);
}
render();
window.addEventListener("resize", () => {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  renderer.setSize(window.innerWidth, window.innerHeight);
  renderer.setPixelRatio(window.devicePixelRatio);
});
